.backstyle {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    /* 가운데 정렬 */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.logo {
    height: auto;
}

.box {
    width: 364px;
    /* height: 307px; */
    background-color: #FAF9F7;
    box-shadow: 2px 2px 15px #0000001a;
    display: inline-flex;
    flex-direction: column;
    padding: 25px;
    font-family: 'NanumSquareNeo', sans-serif;
    border-radius: 10px;
}

.inputs {
    margin-bottom: 10px;
}

.loginbtn {
    height: 35px;
    background-color: #2E5077;
    margin-top: 20px;
    margin-bottom: 10px;
    color: white;
    border-radius: 5px;
    border: none;
    width: 100%;
}

.qbox {
    text-align: center;
    align-content: center;
    margin-top: 30px;
    margin-bottom: 10px;
}

.qbox p {
    margin-bottom: 0;
    color: #7D8A8A;
    cursor: pointer;
    font-family: 'NanumSquareNeo', sans-serif;
}
.qbox p:hover {
    color: #2E5077;
    font-size: 1.05em;
    font-family: 'NanumSquareNeoBold', sans-serif;
}

.qbox button {
    background-color: white;
    border: 1px solid #7D8A8A;
    border-radius: 5px;
    width: 123px;
    height: 35px;
    margin-right: 5px;
    margin-left: 5px;
    margin-top: 0;
}